import { Link } from "react-router-dom";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';

const ListItems = props => {
    return (
        <Link to={{ pathname: `edit/${props.gaun.kode}` }}
            className="col bg-dark bg-opacity-50 rounded position-relative d-flex m-1 p-0 backdrop_blur"
            key={props.gaun._id}
            style={{
                textDecoration: 'none',
                maxHeight: '7rem'
            }}>

            <LazyLoadImage
                height={'100%'}
                alt={props.gaun.kode + 'Image'}
                effect="blur"
                src={`https://drive.google.com/uc?export=view&id=${props.gaun.drive[0]}`} />

            <div className='col-8 d-flex align-items-center p-3'>
                <div>
                    <h4 className='h5 text-white my-0 d-flex mt-1'>{props.gaun.kode}
                        <svg className='svg-star my-0 mx-1' width={24} height={24} fill='gold' style={props.gaun.favorit === false ? { display: 'none' } : { display: 'border-box' }} clipRule="evenodd" fillRule="evenodd" strokeLinejoin="round" strokeMiterlimit="2" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path d="m11.322 2.923c.126-.259.39-.423.678-.423.289 0 .552.164.678.423.974 1.998 2.65 5.44 2.65 5.44s3.811.524 6.022.829c.403.055.65.396.65.747 0 .19-.072.383-.231.536-1.61 1.538-4.382 4.191-4.382 4.191s.677 3.767 1.069 5.952c.083.462-.275.882-.742.882-.122 0-.244-.029-.355-.089-1.968-1.048-5.359-2.851-5.359-2.851s-3.391 1.803-5.359 2.851c-.111.06-.234.089-.356.089-.465 0-.825-.421-.741-.882.393-2.185 1.07-5.952 1.07-5.952s-2.773-2.653-4.382-4.191c-.16-.153-.232-.346-.232-.535 0-.352.249-.694.651-.748 2.211-.305 6.021-.829 6.021-.829s1.677-3.442 2.65-5.44z" fillRule="nonzero" /></svg>
                    </h4>
                    <p className="h6 text-white text-nowrap me-3"><span>Rp. </span>{props.gaun.harga.toLocaleString('id-ID', { minimumFractionDigits: 0 })}</p>
                </div>
            </div>
        </Link>
    )
}

export default ListItems;