import { useEffect, useState } from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';
import axios from 'axios';
import ListItems from './card';
import PageButtons from '../../pagebuttons';
import { SERVERURL } from '../../App';

export default function AdminHome() {
    const navigate = useNavigate();
    const params = useParams();

    const [searchParams] = useSearchParams();
    const [Warna] = useState([]);
    const [gownOrSuit, setGownOrSuit] = useState([]);

    const [sortingType] = useState(searchParams.get('sort') || "FVR");
    const [minHarga] = useState(searchParams.get('hargaMin') || 0);
    const [maxHarga] = useState(searchParams.get('hargaMax') || 10000000);
    const [pageNow, setpageNow] = useState(parseInt(searchParams.get('page')) || 0);
    const [SearchKode, setSearchKode] = useState(searchParams.get('kode') || "");

    const [OnChangeKode, setOnChangeKode] = useState();
    const [loadingState, setLoadingState] = useState(true)

    useEffect(() => {
        const getUrl = () => {
            let filterWarna = [];
            Warna.forEach(element => { if (!element.value) { filterWarna.push(element.warna) } })
            filterWarna = isNaN(filterWarna) ? "&warna=" + filterWarna : "";
            let hargaMin = isNaN(minHarga) ? 0 : minHarga;
            let hargaMax = isNaN(maxHarga) ? 10000000 : maxHarga;

            return {
                page: '?page=' + pageNow,
                sorting: "&sort=" + sortingType,
                kode: SearchKode ? '&kode=' + SearchKode : "",
                kategori: "&kategori=" + params.id,
                filterHarga: "&hargamin=" + hargaMin + "&hargamax=" + hargaMax,
                filterWarna: filterWarna,
            }
        }

        const urlParams = getUrl();
        navigate('?page=' + pageNow + urlParams.kode + urlParams.filterHarga + urlParams.sorting + urlParams.filterWarna);
        let url = SERVERURL + 'api/v1/gown' + urlParams.page + '&gownPerPage=50' + urlParams.kode + urlParams.filterHarga + urlParams.sorting + urlParams.filterWarna;
        const canceltoken = axios.CancelToken.source();
        let listGaunFitlered;

        const getData = async () => {
            await axios.get(url, { cancelToken: canceltoken.token })
                .then(response => {
                    listGaunFitlered = response.data.gownOrSuit;
                    setGownOrSuit(listGaunFitlered);
                })
                .catch((error) => {
                    if (axios.isCancel(error)) {
                        console.log("Cancelled");
                    } else if (error.message === 'Network Error') {
                        alert(
                            'Cant connect to server.\n' +
                            'Please try again later and if problem persist, please contact us through Whatsapp.\n' +
                            '(Link on the bottom of the page)'
                        );
                    } else console.log(error);
                })
                .finally(() =>
                    setTimeout(() => {
                        setLoadingState(false)
                    }, 3000)
                )
        }
        getData();
        return () => canceltoken.cancel();
    }, [pageNow, SearchKode])

    const pageButton = (prevOrNext) => {
        if (prevOrNext) {
            setpageNow(prev => prev + 1)
        } else { pageNow > 0 ? setpageNow(prev => prev - 1) : setpageNow(0) }
    }

    return (
        <div className='container'>
            <form className="row m-1 mx-lg-5 px-lg-5 mt-0 justify-self-end" role="search"
                style={{ zIndex: '10' }}
                onSubmit={() => {
                    setSearchKode(OnChangeKode)
                }}>
                <input className="col rounded border" type="search" placeholder="Search Kode Admin" name="kode" aria-label="Search"
                    onChange={e => {
                        console.log("e.target.value: " + e.target.value)
                        setOnChangeKode(e.target.value)
                    }} />

                <button className="col-auto btn btn-primary mx-1" type="submit">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="white" className="bi bi-search" viewBox="0 0 16 16">
                        <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
                    </svg>
                </button>

                <button className="col-auto btn btn-danger " type="reset"
                    onClick={() => { setSearchKode("") }}>
                    Reset
                </button>

                <button type='button' onClick={() => navigate('add/')} className='btn btn-dark my-2'>
                    Tambah Data Gaun
                </button>
            </form>


            <div className='position-relative container d-flex flex-column'>
                {loadingState ? <span className='display-5 text-center'>Loading...</span> :
                    <div className='row row-cols-1 row-cols-md-3 row-cols-lg-5 justify-content-center '>
                        {gownOrSuit.map(currentgaun => {
                            return <ListItems gaun={currentgaun} key={currentgaun._id} />;
                        })}
                    </div>
                }
            </div>

            <PageButtons pageNow={pageNow} pageButton={pageButton} gownOrSuit={gownOrSuit} />
        </div >

    );
}
